// variables de testimonios
const testimonialsItems = document.querySelectorAll("[data-testimonials-item]");
const modalContainer = document.querySelector("[data-modal-container]");
const overlay = document.querySelector("[data-overlay]");
const modalImg = document.querySelector("[data-modal-img]");
const modalTitle = document.querySelector("[data-modal-title]");
const modalText = document.querySelector("[data-modal-text]");

// función de alternancia de elemento
const elementToggleFunc = function (elem) { elem.classList.toggle("active"); }



// variables de barra lateral
const sidebar = document.querySelector("[data-sidebar]");
const sidebarBtn = document.querySelector("[data-sidebar-btn]");

// funcionalidad de alternancia de barra lateral para dispositivos móviles
sidebarBtn.addEventListener("click", function () { elementToggleFunc(sidebar); });

// portfolio variables
const projectItems = document.querySelectorAll(".project-item");
const modalDescription = document.querySelector(".modal-text .project-description");

// función de alternancia de modal
const toggleModal = function () {
  modalContainer.classList.toggle("active");
  overlay.classList.toggle("active");
};
 

// Función para abrir el modal de proyecto
const openProjectModal = function (projectItem) {
  const projectImg = projectItem.querySelector(".project-img img");
  const projectTitle = projectItem.querySelector(".project-title");
  const projectDescription = projectItem.getAttribute("data-project-description");

  modalImg.src = projectImg.src;
  modalImg.alt = projectTitle.textContent;
  modalTitle.textContent = projectTitle.textContent;
  modalText.innerHTML = projectDescription;

  toggleModal();
};

// agregar evento de clic a los elementos del proyecto
projectItems.forEach(function (projectItem) {
  projectItem.addEventListener("click", function (event) {
    event.preventDefault();
    openProjectModal(projectItem);
  });
});

// blog variables
const blogItems = document.querySelectorAll(".blog-post-item");

// función para abrir el modal de blog
const openBlogModal = function (blogItem) {
  const blogImg = blogItem.querySelector(".blog-banner-box img");
  const blogCategory = blogItem.querySelector(".blog-category");
  const blogDate = blogItem.querySelector("time");
  const blogTitle = blogItem.querySelector(".blog-item-title");
  const blogText = blogItem.querySelector(".blog-text");

  modalImg.src = blogImg.src;
  modalImg.alt = blogTitle.textContent;
  modalTitle.textContent = blogTitle.textContent;
  modalText.innerHTML = `
    <p class="modal-category">${blogCategory.textContent}</p>
    <p class="modal-date">${blogDate.textContent}</p>
    ${blogText.innerHTML}
  `;

  toggleModal();
};

// agregar evento de clic a los elementos del blog
blogItems.forEach(function (blogItem) {
  blogItem.addEventListener("click", function (event) {
    event.preventDefault();
    openBlogModal(blogItem);
  });
});


// función de modal de testimonios
const testimonialsModalFunc = function () {
  toggleModal();
};

// agregar evento de clic a todos los elementos modales
testimonialsItems.forEach(function (testimonialsItem) {
  testimonialsItem.addEventListener("click", function () {
    modalImg.src = this.querySelector("[data-testimonials-avatar]").src;
    modalImg.alt = this.querySelector("[data-testimonials-avatar]").alt;
    modalTitle.innerHTML = this.querySelector("[data-testimonials-title]").innerHTML;
    modalText.innerHTML = this.querySelector("[data-testimonials-text]").innerHTML;
    testimonialsModalFunc();
  });
});

// agregar evento de clic al botón de cierre del modal
const modalCloseBtns = document.querySelectorAll("[data-modal-close-btn]");
modalCloseBtns.forEach(function (btn) {
  btn.addEventListener("click", toggleModal);
});
// Evitar la apertura del modal cuando se hace clic en el elemento2
testimonialsItems.forEach(function (testimonialItem) {
  testimonialItem.addEventListener("click", function (event) {
    event.stopPropagation();
  });
});

// Borrar el contenido del modal al cambiar de secciones
const navigationLinks = document.querySelectorAll("[data-nav-link]");
navigationLinks.forEach(function (link) {
  link.addEventListener("click", function () {
    if (modalTitle) {
      modalTitle.textContent = "";
    }
    if (modalDescription) {
      modalDescription.textContent = "";
    }
    if (modalImg) {
      modalImg.src = "";
      modalImg.alt = "";
    }
  });
});



// custom select variables
const select = document.querySelector("[data-select]");
const selectItems = document.querySelectorAll("[data-select-item]");
const selectValue = document.querySelector("[data-selecct-value]");
const filterBtn = document.querySelectorAll("[data-filter-btn]");

select.addEventListener("click", function () {
  elementToggleFunc(this);
});

// add event in all select items
for (let i = 0; i < selectItems.length; i++) {
  selectItems[i].addEventListener("click", function () {

    let selectedValue = this.innerText.toLowerCase();
    selectValue.innerText = this.innerText;
    elementToggleFunc(select);
    filterFunc(selectedValue);

  });
}

// variables de filtro
const filterItems = document.querySelectorAll("[data-filter-item]");

const filterFunc = function (selectedValue) {

  for (let i = 0; i < filterItems.length; i++) {

    if (selectedValue === "todo") {
      filterItems[i].classList.add("active");
    } else if (selectedValue === filterItems[i].dataset.category) {
      filterItems[i].classList.add("active");
    } else {
      filterItems[i].classList.remove("active");
    }

  }

}

// agregar evento en todos los botones de filtro para pantallas grandes
let lastClickedBtn = filterBtn[0];

for (let i = 0; i < filterBtn.length; i++) {

  filterBtn[i].addEventListener("click", function () {

    let selectedValue = this.innerText.toLowerCase();
    selectValue.innerText = this.innerText;
    filterFunc(selectedValue);

    lastClickedBtn.classList.remove("active");
    this.classList.add("active");
    lastClickedBtn = this;

  });

}



// variables de formulario de contacto
const form = document.querySelector("[data-form]");
const formInputs = document.querySelectorAll("[data-form-input]");
const formBtn = document.querySelector("[data-form-btn]");

// agregar evento a todos los campos de entrada del formulario
for (let i = 0; i < formInputs.length; i++) {
  formInputs[i].addEventListener("input", function () {

    // verificar validación del formulario
    if (form.checkValidity()) {
      formBtn.removeAttribute("disabled");
    } else {
      formBtn.setAttribute("disabled", "");
    }

  });
}



// variables de navegación de página
const pages = document.querySelectorAll("[data-page]");

// agregar evento a todos los enlaces de navegación
for (let i = 0; i < navigationLinks.length; i++) {
  navigationLinks[i].addEventListener("click", function () {

    for (let i = 0; i < pages.length; i++) {
      if (this.innerHTML.toLowerCase() === pages[i].dataset.page) {
        pages[i].classList.add("active");
        navigationLinks[i].classList.add("active");
        window.scrollTo(0, 0);
      } else {
        pages[i].classList.remove("active");
        navigationLinks[i].classList.remove("active");
      }
    }

  });
}

 